import React, { useState } from "react";
import "./App.css";

// IMAGE GENERATOR COMPONENT
const ImageGenerator = ({ prompt, setPrompt, generateImage, image, loading }) => (
  <div className="image-generator">
    <h2 className="image-generator-title">
      <span className="highlight-dark">AI</span> IMAGE GENERATOR
    </h2>
    <div className="input-section">
      <input
        type="text"
        value={prompt}
        onChange={(e) => setPrompt(e.target.value)}
        placeholder="Enter your prompt here..."
      />
      <button onClick={generateImage} disabled={loading}>
        {loading ? "Generating..." : "Generate Image"}
      </button>
    </div>
    {image && <img src={image} alt="Generated" className="generated-image" />}
  </div>
);

// PODCAST GENERATOR COMPONENT
const PodcastGenerator = () => {
  const [pdfFile, setPdfFile] = useState(null);
  const [textInput, setTextInput] = useState("");

  const handlePDFUpload = (e) => {
    setPdfFile(e.target.files[0]);
    setTextInput("");
  };

  const handleTextChange = (e) => {
    setTextInput(e.target.value);
    setPdfFile(null);
  };

  const handleGeneratePodcast = () => {
    if (!pdfFile && !textInput.trim()) {
      alert("Please upload a PDF or enter some text.");
      return;
    }
    alert("Podcast generation started (mock)...");
  };

  return (
    <div className="podcast-generator">
      <h2 className="image-generator-title">
        <span className="highlight-dark">AI</span> PODCAST GENERATOR
      </h2>
      <div className="input-section">
        <label>Upload any Document:</label>
        <input type="file" accept=".pdf" onChange={handlePDFUpload} />

        <p style={{ marginTop: "1rem" }}>Or Enter Text Below:</p>
        <textarea
          rows={6}
          placeholder="Enter your content here..."
          value={textInput}
          onChange={handleTextChange}
          style={{
            backgroundColor: "#2c2c2e",
            color: "#f1f1f1",
            border: "1px solid #444",
            padding: "10px",
            borderRadius: "6px",
            width: "100%",
            resize: "vertical",
          }}
        />
        <button onClick={handleGeneratePodcast} style={{ marginTop: "1rem" }}>
          Generate!
        </button>
      </div>
    </div>
  );
};

// MUSIC GENERATOR COMPONENT
