// DEFAULT PLACEHOLDER FOR OTHER FEATURES
const Placeholder = ({ name }) => (
  <div className="placeholder-feature">
    <h2>{name}</h2>
    <p>This feature is coming soon.</p>
  </div>
);

// MAIN APP
function App() {
  const [prompt, setPrompt] = useState("");
  const [image, setImage] = useState(null);
  const [loading, setLoading] = useState(false);
  const [sidebarOpen, setSidebarOpen] = useState(false);
  const [activeFeature, setActiveFeature] = useState("image-generator");

  const generateImage = async () => {
    setLoading(true);
    try {
      const response = await fetch(
        "https://api-inference.huggingface.co/models/black-forest-labs/FLUX.1-dev",
        {
          method: "POST",
          headers: {
            Authorization: "Bearer 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",
            "Content-Type": "application/json",
          },
          body: JSON.stringify({ inputs: prompt }),
        }
      );

      const contentType = response.headers.get("content-type");
      if (contentType && contentType.includes("application/json")) {
        const json = await response.json();
        alert("Error: " + (json.error || "Unknown error from API"));
        setLoading(false);
        return;
      }

      const blob = await response.blob();
      const imageObjectURL = URL.createObjectURL(blob);
      setImage(imageObjectURL);
    } catch (error) {
      alert("Failed to generate image, please try again.");
    } finally {
      setLoading(false);
    }
  };

  const renderFeature = () => {
    switch (activeFeature) {
      case "image-generator":
        return (
          <ImageGenerator
            prompt={prompt}
            setPrompt={setPrompt}
            generateImage={generateImage}
            image={image}
            loading={loading}
          />
        );
      case "ai-podcast":
        return <PodcastGenerator />;
      case "music-generator":
        return <MusicGenerator />;
      case "ai-hotline":
        return <Placeholder name="AI Hotline" />;
      case "grammar-helper":
        return <Placeholder name="AI Grammar Helper" />;
      case "notes-to-podcast":
        return <Placeholder name="Notes to Podcast" />;
      case "project-helper":
        return <Placeholder name="Project Helper" />;
      case "chatbot":
        return <Placeholder name="AI Chat Bot" />;
      default:
        return <div>Select a feature</div>;
    }
  };

  return (
    <div className="app dark-mode">
      <header className="hero-section">
        <div className="logo-text-container">
          <h1 className="gradient-title">Everything.ai</h1>
          <p className="tagline">simply for everything</p>
          <button
            className="menu-dot"
            onClick={() => setSidebarOpen(!sidebarOpen)}
            aria-label="Toggle menu"
          />
        </div>
      </header>

      <div className="main-layout">
        {sidebarOpen && (
          <aside className="sidebar">
            <h2 className="sidebar-heading">Other Features</h2>
            <ul className="sidebar-menu">
              <li onClick={() => setActiveFeature("image-generator")}>üé® AI Image Generator</li>
              <li onClick={() => setActiveFeature("ai-podcast")}>üéôÔ∏è AI Podcast Generator</li>
              <li onClick={() => setActiveFeature("music-generator")}>üéµ Music Generator</li>
              <li onClick={() => setActiveFeature("ai-hotline")}>üìû AI Hotline</li>
              <li onClick={() => setActiveFeature("grammar-helper")}>‚úçÔ∏è Grammar Helper</li>
              <li onClick={() => setActiveFeature("notes-to-podcast")}>üóíÔ∏è Notes to Podcast</li>
              <li onClick={() => setActiveFeature("project-helper")}>üìö Project Helper</li>
              <li onClick={() => setActiveFeature("chatbot")}>ü§ñ AI Chat Bot</li>
            </ul>
          </aside>
        )}
        <main className="main-content">{renderFeature()}</main>
      </div>
    </div>
  );
}

export default App;
